@import "Components/contact/Left";
@import "Components/contact/Right";
#contact{
    display: flex;
    gap: 6em;
}
#contact-holder{
    position: relative;
}

@media only screen and (max-width: 768px){
    #contact{
        gap: 3em;
        flex-direction: column-reverse;
    }
}
@media only screen and (min-width: 600px) and (max-width: 768px){
    #contact{
        align-items: center;
    }
}
